        font-weight: 700;
        height: 48px;
        border-top: 1px solid #ebebeb;
        box-sizing: border-box;
      }
      .item {
        width: 50%;
        text-align: center;
      }
      .item:first-child {
        text-align: left;
        // padding-left: 13%;
        box-sizing: border-box;
        .ellipsis {
          text-align: left;
        }
      }
      // 大屏
      @media screen and(min-width: 1601px) {
        .item:first-child {
          padding-left: 21%;
        }
      }
      // 小屏
      @media screen and(max-width: 1600px) {
        .item:first-child {
          padding-left: 19%;
        }
      }
    }
    .inner {
      font-size: 16px;
      color: #696969;
      line-height: 48px;
      // letter-spacing: -0.5px;
      // max-width: 427px;
      span {
        box-sizing: border-box;
      }
      li {
        box-sizing: border-box;

        height: 48px;
        border-top: 1px solid #ebebeb;
        .item1 {
          width: 50%;
          text-align: center;
        }
        .item1:first-child {
          text-align: left;
          // padding-left: 13%;
          box-sizing: border-box;
          .ellipsis {
            text-align: left;
          }
        }
        // 大屏
        @media screen and(min-width: 1601px) {
          .item1:first-child {
            padding-left: 21%;
          }
        }
        // 小屏
        @media screen and(max-width: 1600px) {
          .item1:first-child {
            padding-left: 19%;
          }
        }
      }
    }
    // 表格头部
    .tableHead,
    .thead {
      height: 48px;
      line-height: 48px;
      text-align: center;
      background-color: #ebebeb;
      font-weight: 700;
      span.fl {
        width: 50%;
        text-align: center;
      }
    }

    .thead {
      background-color: #fff;
    }
    // 表格底部
    .tableFoot {
      height: 48px;
      line-height: 48px;
      text-align: center;
      background-color: #fff1f0;
      font-weight: 700;
      border-top: 1px solid #ebebeb;
      span.fl {
        width: 50%;
        text-align: center;
      }
    }
    // // 表格
    // .tableHead {
    //   height: 48px;
    //   line-height: 48px;
    //   text-align: center;
    //   background-color: #ebebeb;
    //   font-weight: 700;
    // }
    .el-table .cell,
    .el-table th div,
    .el-table--border td:first-child .cell,
    .el-table--border th:first-child .cell {
      padding-left: 60px;
      font-size: 16px;
    }
    .el-table thead {
      color: #646464;
    }
    .el-table__row td:first-child {
      font-weight: 700;
    }
    .el-table__body-wrapper tbody tr:first-child {
      background: #fff1f0;
      border: 1px solid #ee6723;
    }
    .dt-square-btn {
      width: 34px;
      height: 34px;
      line-height: 30px;
    }
  }
  // 第七个tab
  .seventh {
    .opinion {
      position: relative;
    }
    .promise {
      position: relative;
      border: 1px solid #ccc;
      border-radius: 3px;
      height: 248px;
    }
    .bigInput .el-textarea__inner {
      padding: 50px 15px 100px 40px;
      font-size: 16px;
    }
    // 小屏
    @media screen and (max-width: 1600px) {
      .txt2 {
        bottom: 50px;
      }
      .txt3 {
        bottom: 20px;
      }
    }
    // 大屏
    @media screen and (min-width: 1601px) {
      .txt2 {
        bottom: 80px;
      }
      .txt3 {
        bottom: 45px;
      }
    }
    .txt1 {
      position: absolute;
      left: 40px;
      top: 14px;
      z-index: 10;
      font-size: 18px;
      font-weight: 700;
    }
    .txt2 {
      position: absolute;
      right: 180px;
      // bottom: 80px;
      z-index: 10;
    }
    .txt3 {
      position: absolute;
      right: 100px;
      // bottom: 45px;
      z-index: 10;
    }
    .txt4 {
      position: absolute;
      left: 40px;
      top: 60px;
      z-index: 10;
      padding-right: 100px;
      line-height: 40px;
      text-indent: 2em;
    }
  }
}
</style>





